.nlq-header {
  margin-bottom: 2rem;
}
.nlq-header .breadcrumbs {
  margin-bottom: 1.5rem;
}
.nlq-header .breadcrumbs .breadcrumb-list {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  list-style: none;
  margin: 0;
  padding: 0;
}
.nlq-header .breadcrumbs .breadcrumb-list .breadcrumb-item {
  display: flex;
  align-items: center;
  gap: 0.5rem;
}
.nlq-header .breadcrumbs .breadcrumb-list .breadcrumb-item .breadcrumb-link {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  color: #6b7280;
  text-decoration: none;
  font-size: 0.875rem;
  font-weight: 500;
  transition: color 0.2s ease;
}
.nlq-header .breadcrumbs .breadcrumb-list .breadcrumb-item .breadcrumb-link:hover {
  color: #3b82f6;
}
.nlq-header .breadcrumbs .breadcrumb-list .breadcrumb-item .breadcrumb-link .breadcrumb-icon {
  width: 1rem;
  height: 1rem;
  flex-shrink: 0;
}
.nlq-header .breadcrumbs .breadcrumb-list .breadcrumb-item .breadcrumb-current {
  color: #1f2937;
  font-size: 0.875rem;
  font-weight: 600;
}
.nlq-header .breadcrumbs .breadcrumb-list .breadcrumb-item.active .breadcrumb-current {
  color: #3b82f6;
}
.nlq-header .breadcrumbs .breadcrumb-list .breadcrumb-separator {
  color: #d1d5db;
  display: flex;
  align-items: center;
}
.nlq-header .breadcrumbs .breadcrumb-list .breadcrumb-separator svg {
  width: 1rem;
  height: 1rem;
}
.nlq-header .header-content {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  gap: 2rem;
}
.nlq-header .header-content .header-main {
  display: flex;
  align-items: flex-start;
  gap: 1.5rem;
  flex: 1;
}
.nlq-header .header-content .header-main .header-icon {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 4rem;
  height: 4rem;
  background: linear-gradient(135deg, #3b82f6 0%, #1d4ed8 100%);
  border-radius: 1rem;
  color: white;
  flex-shrink: 0;
  box-shadow: 0 4px 6px -1px rgba(59, 130, 246, 0.1), 0 2px 4px -1px rgba(59, 130, 246, 0.06);
}
.nlq-header .header-content .header-main .header-icon svg {
  width: 2rem;
  height: 2rem;
}
.nlq-header .header-content .header-main .header-text {
  flex: 1;
}
.nlq-header .header-content .header-main .header-text .header-title {
  font-size: 2.25rem;
  font-weight: 700;
  color: #1f2937;
  margin: 0 0 0.75rem 0;
  line-height: 1.2;
}
.nlq-header .header-content .header-main .header-text .header-description {
  font-size: 1.125rem;
  color: #6b7280;
  margin: 0;
  line-height: 1.6;
  max-width: 42rem;
}
.nlq-header .header-content .ai-badge {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.75rem 1rem;
  background: linear-gradient(135deg, #f3f4f6 0%, #e5e7eb 100%);
  border: 1px solid #d1d5db;
  border-radius: 0.75rem;
  color: #374151;
  font-size: 0.875rem;
  font-weight: 600;
  white-space: nowrap;
  box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.05);
}
.nlq-header .header-content .ai-badge .ai-icon {
  width: 1rem;
  height: 1rem;
  color: #3b82f6;
}
.nlq-header .header-content .ai-badge .ai-text {
  font-weight: 600;
}
.nlq-header .api-key-banner {
  margin-top: 1.5rem;
  background: linear-gradient(135deg, #fef3c7 0%, #fde68a 100%);
  border: 1px solid #f59e0b;
  border-radius: 1rem;
  padding: 1rem;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
}
.nlq-header .api-key-banner .banner-content {
  display: flex;
  align-items: flex-start;
  gap: 0.75rem;
}
.nlq-header .api-key-banner .banner-content .banner-icon {
  color: #d97706;
  flex-shrink: 0;
  margin-top: 0.125rem;
}
.nlq-header .api-key-banner .banner-content .banner-text {
  flex: 1;
}
.nlq-header .api-key-banner .banner-content .banner-text .banner-title {
  font-size: 1rem;
  font-weight: 600;
  color: #92400e;
  margin: 0 0 0.25rem 0;
}
.nlq-header .api-key-banner .banner-content .banner-text .banner-description {
  font-size: 0.875rem;
  color: #92400e;
  margin: 0;
  line-height: 1.5;
}

@media (max-width: 768px) {
  .nlq-header .header-content {
    flex-direction: column;
    align-items: stretch;
    gap: 1.5rem;
  }
  .nlq-header .header-content .header-main {
    flex-direction: column;
    align-items: center;
    text-align: center;
    gap: 1rem;
  }
  .nlq-header .header-content .header-main .header-icon {
    width: 3rem;
    height: 3rem;
  }
  .nlq-header .header-content .header-main .header-icon svg {
    width: 1.5rem;
    height: 1.5rem;
  }
  .nlq-header .header-content .header-main .header-text .header-title {
    font-size: 1.875rem;
  }
  .nlq-header .header-content .header-main .header-text .header-description {
    font-size: 1rem;
  }
  .nlq-header .header-content .ai-badge {
    align-self: center;
  }
}
